        <div class="container-fluid">
            <a class="navbar-brand" href="#">Navbar</a>
            <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDropdown"
                aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarNavDropdown">
                <ul class="navbar-nav mx-auto">
                    <li class="nav-item">
                        <a class="nav-link" aria-current="page" href="#">{{ $t('Navbar.home') }}</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="#">{{ $t('Navbar.aboutme') }}</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="#">{{ $t('Navbar.projects') }}</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="#">{{ $t('Navbar.skills') }}</a>
                    </li>
                    <li class="nav-item dropdown">
                        <a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown"
                            aria-expanded="false">
                            {{ $t('Navbar.contactme') }}
                        </a>
                        <ul class="dropdown-menu">
                            <li><a class="dropdown-item" href="#">Action</a></li>
                            <li><a class="dropdown-item" href="#">Another action</a></li>
                            <li><a class="dropdown-item" href="#">Something else here</a></li>
                        </ul>
                    </li>
                    <li class="nav-item">
                        <label>
                            <input type="checkbox" v-model="isEnglish" @change="cambiarIdioma" />
                            Inglés
                        </label>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
</template>

<script>
import { ref } from 'vue';
import { useI18n } from 'vue-i18n';

export default {
    name: 'NavBar',
    setup() {
        const { locale } = useI18n();
        const isEnglish = ref(locale.value === 'en');
        
        const cambiarIdioma = () => {
            locale.value = isEnglish.value ? 'en' : 'es';
        };
        
        return {
            isEnglish,
            cambiarIdioma
        };
    }
}
</script>

