// src/i18n.js
import { createI18n } from 'vue-i18n'
import es from './i18n/es.json'
import en from './i18n/en.json'

const i18n = createI18n({
  legacy: false, // ¡Esto es importante para usar Composition API!
  locale: 'es', // Idioma por defecto
  fallbackLocale: 'es',
  messages: {
    es,
    en
  }
})

export default i18n